import React, { FC, useContext } from 'react';
import { Route, Switch } from 'react-router-dom';

// Move to separate files when number of router increases
import HomePage from 'pages/HomePage';
import BookmarkPage from 'pages/BookmarkPage';
import DetailPage from 'pages/DetailPage';
import SettingPage from 'pages/SettingPage';
import ContextedRouter, { RouterContext } from './ContextedRouter';

export const useRouter = () => useContext(RouterContext);

const Router: FC = props => {
  // throw new Error();
  return (
    <ContextedRouter>
      <Switch>
        <Route path="/detail/:shopID(\d+)" component={DetailPage} />
        <Route exact path="/bookmarks" component={BookmarkPage} />
        <Route exact path="/settings" component={SettingPage} />
        <Route component={HomePage} />
      </Switch>
    </ContextedRouter>
  );
};

export default Router;

