import { ShopState, AppLatLng } from '../types';
import { distance, isInBound } from '../../helpers';
import { getAllStore } from '../../localDB';
import { toRenderUsage } from '../Shop/viewControlMapper';

function wordScore(needle: string, haystack: string = '') {
  // If the entire search term (needle) is found, the score is based on:
  // - length of match as a percentage of haystack (longer is better)
  // - if not a full match, position of match (earlier is better)
  let idx = haystack.indexOf(needle || '');
  if (idx >= 0) {
    let percentage = (haystack.length - needle.length) / haystack.length;
    let pos = (haystack.length - idx) / haystack.length;
    return percentage + ((1 - percentage) * pos) / 3; // never exceeds 1
  }
  // TODO: fuzzy match?
  return 0;
}

function combinedScore(term: string, shop: ShopState) {
  const nameScore = wordScore(term, shop.name.toLowerCase());
  let subtypeScore: number = 0;
  for (const subtype of shop.industrySubtypeList) {
    subtypeScore = Math.max(
      subtypeScore,
      wordScore(term, subtype.toLowerCase())
    );
  }
  // const addrScore = wordScore(term, shop.address);
  const addrScore = 0;
  return Math.max(3 * nameScore, 2 * subtypeScore, addrScore);
}

// Weight linearly by distance, output a [0.5, 1] weighting factor.
function rangeWeight(distance: number, maxRange: number) {
  const scale = maxRange * 2;
  return (scale - distance) / scale;
}

interface searchOptions {
  term: string;
  latlng: AppLatLng;
  northEast: AppLatLng;
  southWest: AppLatLng;
  maxResults?: number;
}

export async function search({
  term,
  latlng,
  northEast,
  southWest,
  maxResults,
}: searchOptions): Promise<ShopState[]> {
  term = term.toLowerCase();
  const maxRange = distance(northEast, southWest);

  const result = await getAllStore('shops');
  const shops: ShopState[] = result.shops.map(toRenderUsage);
  interface TempResult {
    score: number;
    shop: ShopState;
  }
  let results: TempResult[] = [];
  for (let shop of shops) {
    // Filter out shops that are outside the window
    const contains = isInBound(shop.latlng, { northEast, southWest });

    if (!contains) {
      continue;
    }
    if (term !== '') {
      // First try matching the full term.
      let score = combinedScore(term, shop);
      if (score === 0) {
        // Otherwise match by individual words.
        for (let v of term.split(/\s+/)) {
          score += combinedScore(v, shop);
        }
      }

      if (score > 0) {
        const dist = distance(latlng, shop.latlng);
        results.push({
          score: score * rangeWeight(dist, maxRange),
          shop: shop,
        });
        continue;
      }
    } else {
      if (shop.rating >= 0.5)
        results.push({
          score: shop.rating,
          shop: shop,
        });
    }
  }
  results.sort((a, b) => b.score - a.score); // descending order
  if (maxResults && maxResults > 0)
    return results.slice(0, maxResults).map(res => res.shop);
  return results.map(res => res.shop);
}
