import React, { FC, RefObject, useMemo } from 'react';
import { Map as LeafletMap, TileLayer, Marker } from 'react-leaflet';
import { groupBy, max, min } from 'lodash-es';
// import { closeNavMenu } from 'store/NavMenu/actions';
import { iconMyLocation, iconGrey, iconYellow, iconBlue } from './assets';
import { AppLatLng, ShopState } from 'store/types';

type Props = {
  shops: ShopState[];
  selectedShopIndex: number;
  onPinPress: (index: number) => void;
  userLatLng: AppLatLng | null;
  center: AppLatLng;
  zoom: number;
  handlePositionChange: (pos: { center: AppLatLng; zoom: number }) => void;

  onMoved: () => void;
  mapRef: RefObject<LeafletMap>;
};

const MAP_MAX_ZOOM = parseInt(process.env.REACT_APP_MAP_MAX_ZOOM || '20');
const MAP_MIN_ZOOM = parseInt(process.env.REACT_APP_MAP_MIN_ZOOM || '0');
const MAP_SW_MAX = (process.env.REACT_APP_MAP_SW_MAX || '')
  .split(',')
  .map(latlng => parseFloat(latlng));
const MAP_NE_MAX = (process.env.REACT_APP_MAP_NE_MAX || '')
  .split(',')
  .map(latlng => parseFloat(latlng));

const MapComponent: FC<Props> = props => {
  const {
    shops,
    selectedShopIndex,
    onPinPress,
    mapRef,
    onMoved,
    center,
    zoom,
    handlePositionChange,
    userLatLng
  } = props;

  const selectedShop = shops[selectedShopIndex];
  const groupedShopsByLatLng = useMemo(() => {
    const result: {
      [digits: number]: { [roundLatLng: string]: ShopState[] };
    } = {};
    [1, 2, 3, 4, 5, 6].forEach(digits => {
      result[digits] = groupBy(shops, shop => shop.roundKeys[digits]);
    });
    return result;
  }, [shops]);
  const roundNDigit = max([min([Math.round(zoom) - 13, 6]), 3]) as number;
  const renderedShops = groupedShopsByLatLng[roundNDigit];
  return (
    <LeafletMap
      zoomSnap={0.5}
      ref={mapRef}
      maxBoundsViscosity={1}
      maxBounds={[MAP_SW_MAX, MAP_NE_MAX] as [number, number][]}
      center={center}
      zoom={zoom}
      zoomControl={false}
      minZoom={MAP_MIN_ZOOM}
      maxZoom={MAP_MAX_ZOOM}
      onViewportChanged={v => {
        if (v.center && v.zoom) {
          handlePositionChange({
            center: { lat: v.center[0], lng: v.center[1] },
            zoom: v.zoom
          });
          onMoved();
        }
      }}
      onclick={() => {
        // dispatch(closeNavMenu());
      }}
      style={{
        width: '100vw',
        height: '100vh'
      }}
    >
      <TileLayer
        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}-tiles.locationiq.com/v2/obk/r/{z}/{x}/{y}.png?key=5e62082062ad0b"
      />
      {userLatLng && (
        <Marker position={userLatLng} icon={iconMyLocation} zIndexOffset={1000}>
          {/* <Popup>Here</Popup> */}
        </Marker>
      )}

      {/* Do not group shops when zoom to 18 (closest) */}
      {zoom < 18 && selectedShopIndex >= 0 && !!selectedShop && (
        <Marker
          position={selectedShop.latlng}
          icon={
            (selectedShop.side === 'yellow'
              ? iconYellow
              : selectedShop.side === 'blue'
              ? iconBlue
              : iconGrey)[1]
          }
        />
      )}
      {zoom < 18 &&
        Object.entries(renderedShops).map(([latLng, shopsByLatLng]) => {
          // const shopsByLatLng = renderedShops[latLng];
          const firstShop = shopsByLatLng.sort(
            (a, b) => b.rating - a.rating
          )[0];
          let icons = iconGrey;
          if (firstShop.side === 'yellow') {
            icons = iconYellow;
          } else if (firstShop.side === 'blue') {
            icons = iconBlue;
          }
          return (
            <Marker
              key={latLng}
              position={firstShop.latlng}
              icon={icons[0]}
              onclick={() =>
                onPinPress(shops.findIndex(({ id }) => id === firstShop.id))
              }
            />
          );
        })}

      {zoom >= 18 &&
        shops.map((shop, index) => {
          const { id, latlng, side } = shop;
          const selected = selectedShopIndex === index;
          let icons = iconGrey;
          if (side === 'yellow') {
            icons = iconYellow;
          } else if (side === 'blue') {
            icons = iconBlue;
          }
          return (
            <Marker
              key={id}
              position={latlng}
              icon={icons[selected ? 1 : 0]}
              onclick={() => onPinPress(index)}
            />
          );
        })}
    </LeafletMap>
  );
};

export default MapComponent;
